import axios from "axios";
import router from "@/router";
import Vue from "vue";

export default {
  login: function ({ state, dispatch }, payload) {
    let alertConfig = {
      message: "登入成功",
      type: "success",
    };
    return axios
      .post(state.address + state.api_version + state.apis.verification, payload)
      .then((result) => {
        // console.log(result.data.message);
        state.user = result.data.data;
        // store user data as cookie and set axios default auth header
        Vue.$cookies.set("user", state.user);
        axios.defaults.headers.common["Authorization"] =
          "Token " + state.user.token;
        // redirect to dashboard and show login success alert.
        router.push("/").then(() => {
          dispatch("showAlert", alertConfig);
        });
      })
      .catch((error) => {
        alertConfig = {
          message: "登入失敗，請再嘗試一次",
          type: "error",
        };
        dispatch("showAlert", alertConfig);
        console.error(error.response);
      });
  },
  logout: function ({ state }, payload) {
    let user = payload ? payload : state.user;
    return axios
      .get(state.address + state.api_version + state.apis.verification + `${user ? user.id : -1}/`)
      .then((result) => {
        console.log(result.data.message);
        Vue.$cookies.remove("user");
        state.user = {
          id: -1,
          username: "",
          nickname: "",
          token: "",
        };
        router.push("/login");
      })
      .catch((error) => {
        console.error(error.response);
      });
  },
  verify: function ({ state }, payload) {
    let user = payload ? payload : state.user;
    axios.defaults.headers.common["Authorization"] = "Token " + user.token;
    return axios
      .get(state.address + state.api_version + state.apis.verification + `${user ? user.id : -1}/`)
      .then(() => {
        console.log("verify success");
      })
      .catch((error) => {
        console.error(error);
        Vue.$cookies.remove("user");
      });
  },
  changeNickname: function ({ state }, payload) {
    let user = state.user;
    axios.defaults.headers.common["Authorization"] = "Token " + user.token;
    return axios
      .patch(state.address + state.api_version + state.apis.user + `${user ? user.id : -1}/`, payload)
      .then(() => {
        console.log("change success");
      })
      .catch((error) => {
        console.error(error.response);
      });
  },
};
