import axios from "axios";

export default {
  locationsRefresh: function ({ state }) {
    // state.locations.pop();
    return axios
      .get(state.address + state.api_version + state.apis.location)
      .then((result) => {
        state.locations = result.data;
      })
      .catch((error) => {
        console.error(error.response);
      });
  },
};
