import axios from "axios";

export default {
  getRoomExchangeRequest: function ({ state }) {
    return axios
      .get(state.address + state.api_version + state.apis.roomExchangeRequest)
      .then((result) => {
        // console.log(result.data);
        state.roomExchangeRequests = result.data;
      });
  },
  replyExchangeRequest: function ({ state, dispatch }, payload) {
    return axios
      .patch(
        state.address +
        state.api_version +
        state.apis.roomExchangeRequest +
        payload.id +
        "/",
        payload
      )
      .then((result) => {
        console.log(result);
        dispatch("showAlert", {
          message: "回覆成功",
          type: "success",
        });
        dispatch("getRoomExchangeRequest");
      })
      .catch((error) => {
        console.error(error);
        dispatch("showAlert", {
          message: "回覆失敗",
          type: "error",
        });
      });
  },
  makeExchangeRequest: function ({ state, dispatch }, payload) {
    return axios
      .post(
        state.address + state.api_version + state.apis.roomExchangeRequest,
        payload
      )
      .then((result) => {
        console.log(result);
        dispatch("showAlert", {
          message: "申請已送出",
          type: "success",
        });
        // dispatch("getRoomExchangeRequest");
      })
      .catch((error) => {
        console.error(error);
        dispatch("showAlert", {
          message: "此預約已被協議或是已過期",
          type: "error",
        });
      });
  },
  readRoomExchangeRequest: function ({ state, dispatch }, id) {
    return axios
      .get(
        state.address +
        state.api_version +
        state.apis.roomExchangeRequest +
        `${id}/read/`
      )
      .then((result) => {
        console.log(result.data);
        dispatch("getRoomExchangeRequest");
      });
  },
};
