<template>
  <v-row align="center" justify="center">
    <v-col lg="6" md="8" sm="10">
      <v-form ref="loginForm" v-model="formInstance">
        <v-text-field label="用戶名" v-model="user.username"></v-text-field>
        <v-text-field
          label="密碼"
          v-model="user.password"
          type="password"
          @keydown.enter="userLogin"
        ></v-text-field>
        <v-btn @click="userLogin">登入</v-btn>
      </v-form>
    </v-col>
  </v-row>
</template>

<script>
import { mapActions } from "vuex";

export default {
  data() {
    return {
      formInstance: true,
      user: {
        username: "",
        password: "",
      },
    };
  },
  computed: {},
  methods: {
    ...mapActions({
      login: "login",
      showAlert: "showAlert",
    }),
    userLogin: function () {
      this.login(this.user);
    },
  },
};
</script>

<style scoped></style>
