<template>
  <div class="schedule" @click="showDetail = true">
    <v-card>
      <v-card-text>
        <span>
          {{
            reservation.reason.length > 30
              ? reservation.reason.substring(0, 20) + "..."
              : reservation.reason
          }}
        </span>
        <v-divider></v-divider>
        <span>
          {{ `${reservation.start_time}~${reservation.end_time}` }}
        </span>
      </v-card-text>
    </v-card>
    <v-dialog v-model="showDetail" width="unset">
      <v-card>
        <v-card-title>{{ reservation.reason }}</v-card-title>
        <v-card-text>
          <v-list>
            <v-list-item
              class="simple-field"
              v-for="(item, index) in simpleField"
              :key="index"
              v-show="!item.hide"
            >
              <v-list-item-icon>
                <v-icon>{{ item.icon }}</v-icon>
              </v-list-item-icon>

              <v-list-item-content v-text="item.field"></v-list-item-content>
            </v-list-item>
          </v-list>
        </v-card-text>
        <v-card-actions>
          <v-btn
            class="red white--text"
            @click="deleteReservation({ id: reservation.id })"
            v-if="reservation.user_id === user.id"
            text
          >
            刪除
          </v-btn>
          <v-btn
            class="orange lighten-3"
            @click="makeExchange"
            v-if="reservation.user_id !== user.id"
          >
            協調
          </v-btn>
          <v-btn @click="showDetail = false" text outlined>關閉</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <room-exchange-form
      type="REQUEST"
      :show.sync="showExchangeForm"
      :request-data="reservation"
    ></room-exchange-form>
    <!--    {{ book }}-->
  </div>
</template>

<script>
import RoomExchangeForm from "@/components/RoomExchangeForm";
import { mapActions, mapState } from "vuex";

export default {
  props: ["reservation"],
  components: { RoomExchangeForm },
  computed: {
    ...mapState(["meetingRooms", "user"]),
    simpleField: function () {
      return [
        {
          icon: "mdi-clock-outline",
          field: `${this.reservation.start_time}~${this.reservation.end_time}`,
        },
        {
          icon: "mdi-calendar-today",
          field: this.reservation["use_date"],
        },
        {
          icon: "mdi-map-marker",
          field: this.reservation["room_name"],
        },
        {
          hide: this.reservation["host"].length === 0,
          icon: "mdi-account",
          field: this.reservation["host"],
        },
        {
          hide: !(this.reservation["attendee"] || this.reservation["guest"]),
          icon: "mdi-account-multiple",
          field: `${this.reservation["attendee"]} 位同仁 ${this.reservation["guest"]} 位來賓`,
        },
        {
          hide: !(
            this.reservation["coffee"] ||
            this.reservation["tea"] ||
            this.reservation["water"]
          ),
          icon: "mdi-coffee",
          field: `${this.reservation["coffee"]} 杯咖啡 ${this.reservation["water"]} 杯水 ${this.reservation["tea"]} 杯茶`,
        },
        {
          hide: !(
            this.reservation["meal_courtyard"] ||
            this.reservation["meal_buffet"]
          ),
          icon: "mdi-silverware-fork-knife",
          field: `中庭 ${this.reservation["meal_courtyard"]}人 自助餐${this.reservation["meal_buffet"]}人`,
        },
        {
          hide: !this.reservation["note"],
          icon: "mdi-note-edit",
          field: this.reservation["note"],
        },
        {
          icon: "mdi-phone",
          field: this.reservation["reservation_user_nickname"],
        },
      ];
    },
  },
  data() {
    return {
      showDetail: false,
      showExchangeForm: false,
    };
  },
  methods: {
    ...mapActions({
      deleteReservation: "deleteReservation",
    }),
    makeExchange: function () {
      this.showExchangeForm = true;
    },
  },
};
</script>

<style scoped>
.schedule {
  margin: 3px 0;
}
</style>
