<template>
  <v-menu :close-on-content-click="false" v-model="myPickerSwitch">
    <template v-slot:activator="{ on }">
      <v-text-field
        v-model="myDateModel"
        v-on="on"
        :label="label"
        :rules="rules"
        clearable
        readonly
      >
      </v-text-field>
    </template>
    <v-date-picker
      v-model="myDateModel"
      @input="myPickerSwitch = false"
      :min="minDate"
    >
    </v-date-picker>
  </v-menu>
</template>

<script>
export default {
  props: ["dateModel", "pickerSwitch", "label", "max", "min", "rules"],
  computed: {
    myDateModel: {
      get() {
        return this.dateModel;
      },
      set(value) {
        this.$emit("update:dateModel", value);
      },
    },
    minDate: function () {
      return this.min ? this.min : "";
    },
    formRule: function () {
      return this.rules ? this.rules : [];
    },
  },
  data() {
    return {
      myPickerSwitch: false,
    };
  },
};
</script>

<style scoped></style>
