<template>
  <form>
    <v-dialog v-model="showReserveForm" width="unset">
      <v-card>
        <v-card-title>{{ `${filteredRoomName} - 租借表單` }}</v-card-title>
        <v-card-text>
          <v-form ref="formValidate" v-model="formInstance" lazy-validation>
            <v-row justify="center">
              <v-col id="use_date" cols="10">
                <my-date-picker
                  :date-model.sync="form.use_date"
                  :rules="formRules.useDateRule"
                  label="使用日期"
                >
                </my-date-picker>
              </v-col>

              <v-col id="startTimePicker" cols="5">
                <v-menu
                  :close-on-content-click="false"
                  v-model="startTimePicker"
                  min-width="auto"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="form.start_time"
                      label="開始時間"
                      v-on="on"
                      :rules="formRules.startTimeRule"
                      clearable
                      readonly
                    ></v-text-field>
                  </template>
                  <v-time-picker
                    v-model="form.start_time"
                    @input="startTimePicker = false"
                    min="08:00"
                    max="22:00"
                    :allowed-minutes="(m) => m % 15 === 0"
                  ></v-time-picker>
                </v-menu>
              </v-col>

              <v-col id="endTimePicker" cols="5">
                <v-menu
                  :close-on-content-click="false"
                  v-model="endTimePicker"
                  min-width="auto"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="form.end_time"
                      label="結束時間"
                      :rules="formRules.endTimeRule"
                      v-on="on"
                      clearable
                      readonly
                    ></v-text-field>
                  </template>
                  <v-time-picker
                    v-model="form.end_time"
                    @input="endTimePicker = false"
                    :min="form.start_time"
                    max="22:00"
                    :allowed-minutes="(m) => m % 15 === 0"
                  ></v-time-picker>
                </v-menu>
              </v-col>

              <v-col id="reason" cols="10">
                <v-text-field
                  label="用途說明"
                  v-model="form.reason"
                  :rules="formRules.reasonRule"
                ></v-text-field>
              </v-col>

              <v-col id="reason" cols="10">
                <v-text-field label="主持人" v-model="form.host"></v-text-field>
              </v-col>

              <v-col id="options" cols="10">
                <v-row>
                  <v-col cols="4">
                    <v-checkbox
                      label="與會者"
                      v-model="fieldSwitch.attendee"
                    ></v-checkbox>
                  </v-col>
                  <v-col cols="4">
                    <v-checkbox
                      label="茶水"
                      v-model="fieldSwitch.water"
                    ></v-checkbox>
                  </v-col>
                  <v-col cols="4">
                    <v-checkbox
                      label="訂餐"
                      v-model="fieldSwitch.meal"
                    ></v-checkbox>
                  </v-col>
                </v-row>
              </v-col>

              <v-col
                id="attendee-and-guest"
                cols="10"
                v-if="fieldSwitch.attendee"
              >
                <v-row justify="center">
                  <v-col id="attendee" cols="6">
                    <v-text-field
                      label="與會同仁"
                      v-model="form.attendee"
                      type="number"
                    ></v-text-field>
                  </v-col>
                  <v-col id="guest" cols="6">
                    <v-text-field
                      label="與會來賓"
                      v-model="form.guest"
                      type="number"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-col>

              <v-col
                id="water-tea-and-coffee"
                cols="10"
                v-if="fieldSwitch.water"
              >
                <v-row>
                  <v-col cols="4">
                    <v-text-field
                      label="水"
                      v-model="form.water"
                      type="number"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="4">
                    <v-text-field label="茶" v-model="form.tea" type="number">
                    </v-text-field>
                  </v-col>
                  <v-col cols="4">
                    <v-text-field
                      label="咖啡"
                      v-model="form.coffee"
                      type="number"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-col>

              <v-col id="meal" cols="10" v-if="fieldSwitch.meal">
                <v-row justify="center">
                  <v-col id="meal-courtyard" cols="6">
                    <v-text-field
                      label="中庭"
                      v-model="form.meal_courtyard"
                      type="number"
                    ></v-text-field>
                  </v-col>
                  <v-col id="meal-buffet" cols="6">
                    <v-text-field
                      label="自助餐"
                      v-model="form.meal_buffet"
                      type="number"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-col>

              <v-col id="note" cols="10">
                <v-text-field label="備註" v-model="form.note"></v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-btn @click="close">關閉表單</v-btn>
          <v-btn @click="validateAndSend">送出預約</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </form>
</template>

<script>
import { mapActions, mapState } from "vuex";
import MyDatePicker from "@/components/MyDatePicker";

export default {
  props: ["show", "reserveData"],
  components: { MyDatePicker },
  computed: {
    ...mapState({
      rooms: "meetingRooms",
      reservations: "reservations",
      times: "times",
      user: "user",
    }),
    room_id: function () {
      return this.reserveData.room_id;
    },
    showReserveForm: {
      get() {
        return this.show;
      },
      set(value) {
        this.$emit("update:show", value);
      },
    },
    filteredRoomName: function () {
      if (!this.showReserveForm) {
        return "";
      }
      let filteredRoom = this.rooms.filter(
        (room) => room.id === this.reserveData["room_id"]
      );
      return filteredRoom.length ? filteredRoom[0]["room_name"] : "";
    },
  },
  watch: {
    "validate.use_date": function () {
      console.log(this.form.use_date);
    },
    showReserveForm: {
      handler: function () {
        this.form.use_date = this.reserveData.use_date;
        this.form.start_time = this.reserveData.start_time;
        this.form.end_time = this.reserveData.end_time;
      },
    },
  },
  data() {
    return {
      datePicker: false,
      startTimePicker: false,
      endTimePicker: false,
      fieldSwitch: {
        attendee: false,
        water: false,
        meal: false,
      },
      formInstance: true,
      form: {
        reason: "",
        use_date: new Date().toLocaleDateString("zh-tw").replaceAll("/", "-"),
        // use_time: "",
        start_time: "",
        end_time: "",
        host: "",
        attendee: 0,
        guest: 0,
        tea: 0,
        water: 0,
        coffee: 0,
        water_dispenser: false,
        meal_courtyard: 0,
        meal_buffet: 0,
        note: "",
      },
      formRules: {
        useDateRule: [(v) => !!v || "請選擇日期"],
        reasonRule: [(v) => !!v || "請輸入用途說明"],
        startTimeRule: [
          (v) => !!v || "請選擇開始時間",
          (v) => {
            let choseHour = parseInt(v.split(":")[0]);
            return (
              choseHour > new Date().getHours() ||
              "選擇時間已過，請重新選擇開始時間"
            );
          },
        ],
        endTimeRule: [
          (v) => !!v || "請選擇結束時間",
          (v) => {
            let choseHour = parseInt(v.split(":")[0]);
            return (
              choseHour > new Date().getHours() ||
              "選擇時間已過，請重新選擇結束時間"
            );
          },
        ],
      },
    };
  },
  methods: {
    ...mapActions({
      newReservation: "newReservation",
      showAlert: "showAlert",
      roomsRefresh: "roomsRefresh",
    }),
    validateAndSend: function () {
      // check reservation time is not overlap with other reservation
      if (!this.$refs.formValidate.validate()) {
        return;
      }
      this.form["room_id"] = this.room_id;
      this.form["user_id"] = this.user.id;
      // this.form["use_time"] = this.startTime.toString() + "~" + this.endTime.toString();
      this.newReservation(this.form).then((result) => {
        console.log(result);
        this.showReserveForm = false;
      });
    },
    close: function () {
      this.showReserveForm = false;
      this.$refs.formValidate.resetValidation();
      this.$refs.formValidate.reset();
    },
  },
};
</script>

<style scoped></style>
