<template>
  <v-row>
    <v-col cols="12">
      <!--simple apps-->
      <v-tooltip v-for="(app, index) in appsDetail" :key="index" bottom>
        <template v-slot:activator="{ on, attrs }">
          <v-badge
            :color="app.color ? app.color : 'warning'"
            :content="app.content.toString()"
            :value="app.value"
            bordered
            overlap
          >
            <v-btn v-bind="attrs" :to="app.to" v-on="on" x-large icon>
              <v-icon>{{ app.icon }}</v-icon>
            </v-btn>
          </v-badge>
        </template>
        <span>{{ app.hint }}</span>
      </v-tooltip>
      <!--      {{ roomExchangeRequests }}-->
    </v-col>
  </v-row>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  mounted() {
    this.getRoomExchangeRequest();
    this.reservationsRefresh();
  },
  computed: {
    ...mapState({
      roomExchangeRequests: "roomExchangeRequests",
      reservations: "reservations",
      user: "user",
    }),
    appsDetail: function () {
      return [
        {
          icon: "mdi-calculator-variant",
          to: "/account",
          hint: "會計",
          value: 0,
          content: 0,
        },
        { icon: "mdi-account", to: "/hr", hint: "人資", value: 0, content: 0 },
        {
          icon: "mdi-file",
          to: "/project",
          hint: "專案",
          value: 0,
          content: 0,
        },
        {
          icon: "mdi-plus",
          to:
            this.hasTodayReservation || this.hasReservations
              ? "/calendar"
              : "/available-room",
          hint: "借用會議室",
          value: this.hasReservations,
          content: "",
          color: this.hasTodayReservation ? "error" : "",
        },
        {
          icon: "mdi-handshake",
          to: "/room-exchange",
          hint: "協調會議室",
          value: this.exchangeRequestCount,
          content: this.exchangeRequestCount,
        },
      ];
    },
  },
  watch: {
    roomExchangeRequests: {
      handler: function () {
        // console.log(this.roomExchangeRequests);
        let requests = this.roomExchangeRequests.filter((request) => {
          // use to simplify condition.
          let isReplier = request.replier_id === this.user.id;
          let isRequester = request.requester_id === this.user.id;
          let isRequesting = request.status === "REQUESTING";
          // filter condition.
          return (isRequester && !isRequesting) || (isReplier && isRequesting);
        });
        this.exchangeRequestCount = requests.length;
      },
      deep: true,
    },
    reservations: {
      handler: function () {
        let userOwnedAvailableReservations = this.reservations.filter(
          (reservation) => {
            // use to simplify condition.
            let use_date = new Date(reservation.use_date);
            let today = new Date(new Date().toLocaleDateString());
            // filter condition.
            return reservation.user_id === this.user.id && use_date > today;
          }
        );
        let userTodayReservations = userOwnedAvailableReservations.filter(
          (reservation) => {
            // use to simplify condition.
            let todayStr = new Date().toLocaleDateString().replaceAll("/", "-");
            // filter condition.
            return reservation.use_date === todayStr;
          }
        );
        this.hasReservations = userOwnedAvailableReservations.length > 0;
        this.hasTodayReservation = userTodayReservations.length > 0;
      },
      deep: true,
    },
  },
  data() {
    return {
      hasReservations: false,
      hasTodayReservation: false,
      exchangeRequestCount: 0,
      acceptedRequestCount: 0,
      rejectedRequestCount: 0,
    };
  },
  methods: {
    ...mapActions({
      getRoomExchangeRequest: "getRoomExchangeRequest",
      reservationsRefresh: "reservationsRefresh",
    }),
  },
};
</script>

<style scoped></style>
