import axios from "axios";

export default {
  roomsRefresh: function ({ state }) {
    return axios
      .get(state.address + state.api_version + state.apis.room)
      .then((result) => {
        state.meetingRooms = result.data;
      })
      .catch((error) => {
        console.error(error);
      });
  },
};
