import Vue from "vue";
import VueRouter from "vue-router";
import Calendar from "../views/Calendar";
import AvailableRoom from "@/views/RoomReserve";
import Login from "@/views/Login";
import Dashboard from "@/views/Dashboard";
import RoomExchange from "@/views/RoomExchange";
import Setting from "@/views/Setting";

import store from "@/store";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Dashboard,
  },
  {
    path: "/available-room",
    name: "AvailableRoom",
    component: AvailableRoom,
  },
  {
    path: "/calendar",
    name: "Calendar",
    component: Calendar,
    meta: {
      auth: true,
    },
  },
  {
    path: "/login",
    name: "Login",
    component: Login,
  },
  {
    path: "/dashboard",
    name: "Dashboard",
    component: Dashboard,
  },
  {
    path: "/room-exchange",
    name: "RoomExchange",
    component: RoomExchange,
  },
  {
    path: "/setting",
    name: "Setting",
    component: Setting,
  },
];

let router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach(async (to, from, next) => {
  console.clear();
  if (!store.getters.isVerified) {
    let user = Vue.$cookies.get("user");
    if (user) {
      await store.dispatch("verify", user).then(() => {
        store.state.user = user;
      });
    }
  }
  if (!store.getters.isVerified && to.name !== "Login") {
    next({ name: "Login" });
  } else if (store.getters.isVerified && to.name === "Login") {
    next({ name: "DashBoard" });
  } else {
    next();
  }
});

export default router;
