let inTime = function (startTime, endTime, time) {
  // console.log(startTime, endTime, time);
  time = time.toString();
  let startTimeObj = {
    hour: parseInt(startTime.split(":")[0]),
    minute: parseInt(startTime.split(":")[1]),
  };
  let endTimeObj = {
    hour: parseInt(endTime.split(":")[0]),
    minute: parseInt(endTime.split(":")[1]),
  };
  let checkTime = {
    hour: parseInt(time.split(":")[0]),
    minute: parseInt(time.split(":")[1]),
  };
  if (startTimeObj.hour === checkTime.hour) {
    if (startTimeObj.minute > checkTime.minute) {
      return false;
    }
  } else if (startTimeObj.hour > checkTime.hour) {
    return false;
  }

  if (endTimeObj.hour === checkTime.hour) {
    if (endTimeObj.minute < checkTime.minute) {
      return false;
    }
  } else if (endTimeObj.hour < checkTime.hour) {
    return false;
  }
  return true;
};

let inTimeTest = function () {
  let startTime = "08:00";
  let endTime = "12:00";
  let checkTimes = ["08:00", "09:00", "13:00"];
  let passFlag = true;
  checkTimes.forEach((time) => {
    let isPass = inTime(startTime, endTime, time);
    console.log(
      `timePeriod:${startTime}~${endTime}, checkTime:${time}
        , result:${inTime(startTime, endTime, time)}`
    );
    passFlag = passFlag && isPass;
  });
  return passFlag;
};

export default {
  inTime: inTime,
  inTimeTest: inTimeTest,
};
