<template>
  <v-row>
    <v-col cols="12">
      <page-title title="會議室列表"></page-title>
    </v-col>
    <v-col cols="12">
      <v-card>
        <v-card-subtitle id="location-tabs">
          <v-tabs>
            <v-tab
              v-for="(location, index) in locations"
              :key="index"
              @click="locationID = location['id']"
            >
              {{ location["location_name"] }}
            </v-tab>
          </v-tabs>
          <v-divider></v-divider>
        </v-card-subtitle>
        <v-card-text id="available-meeting-room">
          <available-meeting-room
            v-for="(date, index) in dateWeekRange"
            :key="index"
            :the-date="date"
            :location-id="locationID"
          >
          </available-meeting-room>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import AvailableRoomBlock from "@/components/AvailableRoomBlock";
import PageTitle from "@/components/PageTitle";
import { mapActions, mapState } from "vuex";

export default {
  components: { AvailableMeetingRoom: AvailableRoomBlock, PageTitle },
  mounted() {
    this.locationsRefresh();
    this.reservationsRefresh();
  },
  data() {
    return {
      locationID: 1,
      tab_id: 0,
    };
  },
  computed: {
    ...mapState({
      meetingRooms: "meetingRooms",
      locations: "locations",
    }),
    dateWeekRange: function () {
      let dates = [];
      for (let i = 0; i < 7; i++) {
        let today = new Date();
        today.setDate(today.getDate() + i);
        dates.push(today.toLocaleDateString());
      }
      return dates;
    },
  },
  watch: {
    locations: {
      handler: function () {
        this.locationID = this.locations.length ? this.locations[0].id : -1;
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    ...mapActions({
      locationsRefresh: "locationsRefresh",
      reservationsRefresh: "reservationsRefresh",
    }),
  },
};
</script>

<style scoped>
#available-switch > * {
  margin: 0 5px;
}
</style>
