<template>
  <v-row justify="center">
    <v-col cols="12">
      <page-title title="待處理之會議室交換請求"></page-title>
    </v-col>
    <v-col cols="12">
      <v-card>
        <v-card-text>
          <v-simple-table v-if="requestingRoomExchangeRequests.length">
            <thead>
              <tr>
                <th
                  v-for="(header, index) in requestingTableHeader"
                  :key="index"
                >
                  {{ header }}
                </th>
                <th>回覆</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(request, index) in requestingRoomExchangeRequests"
                :key="index"
              >
                <td>
                  {{ request.requester_name }}
                </td>
                <td>
                  {{ request.room_name }}
                </td>
                <td>
                  {{ request.reason }}
                </td>
                <td>
                  {{ request.reservation_date }}
                </td>
                <td>
                  {{
                    request.reservation_start_time +
                    "~" +
                    request.reservation_end_time
                  }}
                </td>
                <td>
                  <v-btn @click="replyExchangeRequest(request)">回覆</v-btn>
                </td>
              </tr>
            </tbody>
          </v-simple-table>
          <span v-if="!requestingRoomExchangeRequests.length">無</span>
        </v-card-text>
      </v-card>
    </v-col>
    <v-col cols="12"></v-col>
    <v-col cols="12">
      <page-title title="已回覆之會議室交換請求"></page-title>
    </v-col>
    <v-col cols="12">
      <v-card>
        <v-card-text>
          <v-simple-table v-if="notRequestingRoomExchangeRequests.length">
            <thead>
              <tr>
                <th
                  v-for="(header, index) in notRequestingTableHeader"
                  :key="index"
                >
                  {{ header }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(request, index) in notRequestingRoomExchangeRequests"
                :key="index"
                class="white--text lighten-1"
                :class="request.status === 'ACCEPT' ? 'green' : 'red'"
              >
                <td>
                  {{ request.status === "ACCEPT" ? "接受" : "拒絕" }}
                </td>
                <td>
                  {{ request.room_name }}
                </td>
                <td>
                  {{ request.reservation_date }}
                </td>
                <td>
                  {{
                    request.reservation_start_time +
                    "~" +
                    request.reservation_end_time
                  }}
                </td>
                <td>
                  {{ request.reason }}
                </td>
                <td>
                  {{ request.reply }}
                </td>
                <td>
                  <v-btn @click="readRequest(request.id)" color="white" icon>
                    <v-icon>mdi-check</v-icon>
                  </v-btn>
                </td>
              </tr>
            </tbody>
          </v-simple-table>
          <span v-if="!notRequestingRoomExchangeRequests.length">無</span>
        </v-card-text>
      </v-card>
    </v-col>
    <v-col cols="12">
      <room-exchange-form
        :show.sync="showExchangeForm"
        :request-data="requestData"
        type="REPLY"
      ></room-exchange-form>
    </v-col>
  </v-row>
</template>

<script>
import PageTitle from "@/components/PageTitle";
import RoomExchangeForm from "@/components/RoomExchangeForm";
import { mapActions, mapState } from "vuex";

export default {
  components: {
    PageTitle,
    RoomExchangeForm,
  },
  mounted() {
    this.getRoomExchangeRequest();
  },
  computed: {
    ...mapState(["roomExchangeRequests", "user"]),
    requestingRoomExchangeRequests: function () {
      return this.roomExchangeRequests
        .filter((request) => {
          return (
            request.status === "REQUESTING" &&
            request.replier_id === this.user.id
          );
        })
        .reverse();
    },
    notRequestingRoomExchangeRequests: function () {
      return this.roomExchangeRequests
        .filter((request) => {
          return (
            request.status !== "REQUESTING" &&
            request.requester_id === this.user.id
          );
        })
        .reverse();
    },
  },
  data() {
    return {
      requestingTableHeader: ["請求人", "會議室", "交換理由", "日期", "時段"],
      notRequestingTableHeader: [
        "請求結果",
        "會議室",
        "日期",
        "時段",
        "協調理由",
        "回覆",
        "已讀訊息",
      ],
      showExchangeForm: false,
      requestData: null,
    };
  },
  methods: {
    ...mapActions({
      getRoomExchangeRequest: "getRoomExchangeRequest",
      readRoomExchangeRequest: "readRoomExchangeRequest",
    }),
    replyExchangeRequest: function (request) {
      this.requestData = request;
      this.showExchangeForm = true;
    },
    readRequest: function (id) {
      this.readRoomExchangeRequest(id);
    },
  },
};
</script>

<style scoped></style>
