import axios from "axios";

export default {
  getMessages: function ({ state }) {
    return axios
      .get(state.address + state.api_version + state.apis.messages)
      .then((result) => {
        state.messages = result.data;
        // console.log(result.data);
      })
      .catch((error) => {
        console.error(error.response);
      });
  },
};
