import axios from "axios";
// import router from "@/router";

export default {
  // read
  reservationsRefresh: function ({ state }) {
    return axios
      .get(state.address + state.api_version + state.apis.reservation)
      .then((result) => {
        state.reservations = result.data.map((book) => {
          // book.use_date = new Date(book.use_date);
          return book;
        });
      });
  },
  // todo: ranged reservation
  // rangedReservations: function ({ state }, params) {
  //   return axios.get(state.address + state.api_version + state.apis.reservation, { params: params })
  //     .then((result) => {
  //       state.reservations = result.data.map((book) => {
  //         // book.use_date = new Date(book.use_date);
  //         state.showReverseForm = false;
  //         return book;
  //       });
  //     });
  // },
  newReservation: function ({ state, dispatch }, payload) {
    return axios
      .post(state.address + state.api_version + state.apis.reservation, payload)
      .then((result) => {
        console.log(result.data.message);
        dispatch("reservationsRefresh");
        dispatch("showAlert", {
          message: "預約成功",
          type: "success",
        });
      })
      .catch((error) => {
        let message = "";
        switch (error.response.status) {
          case 400:
            message = "請勿嘗試預約過去的時間";
            break;
          case 409:
            message = "預約時間已被其他人預約，請參考預約時間表";
            break;
          default:
            message = "預約失敗";
        }
        dispatch("showAlert", {
          message: message,
          type: "error",
        });
        console.error(error.response);
      });
  },
  deleteReservation: function ({ state, dispatch }, payload) {
    return axios
      .delete(
        state.address +
          state.api_version +
          state.apis.reservation +
          payload.id +
          "/"
      )
      .then((result) => {
        console.log(result.data.message);
        dispatch("reservationsRefresh").then(() => {
          dispatch("showAlert", {
            message: "刪除預約成功",
            type: "success",
          });
          // location.reload();
        });
      })
      .catch((error) => {
        dispatch("showAlert", {
          message: "刪除預約失敗",
          type: "error",
        });
        console.error(error.response);
      });
  },
};
