import timeProcess from "@/store/common/timeProcess";

export default {
  unavailableRooms: function (state) {
    let nowDate = new Date().toLocaleDateString("zh-tw").replaceAll("/", "-");
    let date = new Date();
    let hour = date.getHours();
    let minute = date.getMinutes();
    let nowTime =
      (hour < 10 ? "0" : "") +
      hour.toString() +
      ":" +
      (minute < 10 ? "0" : "") +
      minute.toString();
    return state.reservations.filter((r) => {
      // console.log(r.use_date, nowDate);
      if (r.use_date !== nowDate) {
        return false;
      }
      // console.log(r.start_time, r.end_time, nowTime);
      return timeProcess.inTime(r.start_time, r.end_time, nowTime);
    });
  },
};
