<template>
  <v-card style="margin: 10px 0">
    <v-card-subtitle :class="theDate === today ? todayClass : notTodayClass">
      <span>
        {{ (theDate === today ? "今天 " : "") + theDate }}
      </span>
    </v-card-subtitle>
    <v-card-text>
      <v-row>
        <v-col cols="12">
          <span v-if="!filteredByLocationRooms.length">
            {{ `目前無可用的會議室` }}
          </span>
        </v-col>

        <v-col
          v-for="(room, index) in filteredByLocationRooms"
          :key="index"
          dense
        >
          <v-card>
            <v-card-text>
              <span>{{ room["room_name"] }}</span>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
              <v-btn
                :class="
                  checkedPeriod[room.id]['morning']
                    ? unavailableClass
                    : availableClass
                "
                @click="openReverseFromDialog(room.id, '8:00', '12:00')"
                :disabled="new Date().getHours() > 12 && theDate === today"
                text
              >
                預約上午
              </v-btn>
              <v-btn
                :class="
                  checkedPeriod[room.id]['afternoon']
                    ? unavailableClass
                    : availableClass
                "
                @click="openReverseFromDialog(room.id, '13:00', '17:00')"
                :disabled="new Date().getHours() > 18"
                text
              >
                預約下午
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
        <v-col cols="12">
          <reserve-form
            :show.sync="showReverseForm"
            :reserve-data="reservationData"
          ></reserve-form>
        </v-col>
      </v-row>
    </v-card-text>
    <!--    {{ checkedPeriod }}-->
  </v-card>
</template>

<script>
import ReserveForm from "@/components/ReserveForm";
import { mapActions, mapState } from "vuex";

export default {
  props: ["locationId", "theDate"],
  components: { ReserveForm },
  computed: {
    ...mapState({
      locations: "locations",
      meetingRooms: "meetingRooms",
    }),
    reservations: {
      get() {
        return this.$store.state.reservations.filter((reservation) => {
          // console.log(reservation.use_date, this.theDate.replaceAll("/", "-"));
          return reservation.use_date === this.theDate.replaceAll("/", "-");
        });
      },
    },
    checkedPeriod: function () {
      let checkedPeriod = {};
      this.meetingRooms.forEach((room) => {
        checkedPeriod[room.id] = {};
      });
      this.reservations.forEach((reservation) => {
        let startHour = parseInt(reservation.start_time.split(":")[0]);
        let endHour = parseInt(reservation.end_time.split(":")[0]);
        // use to simplify condition.
        let isMorning =
          (8 <= startHour && startHour <= 12) ||
          (8 <= endHour && endHour <= 12);
        let isAfternoon =
          (13 <= startHour && startHour <= 17) ||
          (13 <= endHour && endHour <= 17);
        // condition calculate.
        checkedPeriod[reservation.room_id]["morning"] ||= isMorning;
        checkedPeriod[reservation.room_id]["afternoon"] ||= isAfternoon;
        // console.log(startHour, endHour, isMorning, isAfternoon);
      });
      return checkedPeriod;
    },
    filteredByLocationRooms: function () {
      return this.meetingRooms.filter(
        (room) => room.location_id === this.locationId
      );
    },
  },
  data() {
    return {
      today: new Date().toLocaleDateString(),
      todayClass: "success white--text",
      notTodayClass: "grey white--text",
      availableClass: "success white--text",
      unavailableClass: "error white--text",
      showReverseForm: false,
      reservationData: null,
    };
  },
  methods: {
    ...mapActions(["locationsRefresh"]),
    // tab update function
    openReverseFromDialog: function (room_id, start_time, end_time) {
      this.reservationData = {
        room_id: room_id,
        use_date: this.theDate.replaceAll("/", "-"),
        start_time: start_time,
        end_time: end_time,
      };
      this.showReverseForm = true;
    },
  },
};
</script>

<style scoped></style>
