<template>
  <v-dialog v-model="formShow" width="unset">
    <v-card>
      <v-card-title>
        <span>會議室交換</span>
      </v-card-title>
      <v-card-text>
        <v-form ref="replyForm" v-if="type === 'REPLY'">
          <v-text-field
            label="接受/拒絕 理由"
            v-model="reply"
            :rules="replyRules"
          ></v-text-field>
        </v-form>
        <v-form ref="requestForm" v-if="type === 'REQUEST'">
          <v-text-field
            label="協調理由"
            @keydown.enter="makeRequest"
            v-model="reason"
            :rules="reasonRules"
          ></v-text-field>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-btn
          class="blue lighten-3 white--text"
          @click="makeRequest"
          v-if="type === 'REQUEST'"
          outlined
        >
          申請
        </v-btn>
        <v-btn
          class="blue lighten-3 white--text"
          @click="replyRequest('ACCEPT')"
          v-if="type === 'REPLY'"
          outlined
        >
          接受
        </v-btn>
        <v-btn
          class="red lighten-3 white--text"
          @click="replyRequest('REJECT')"
          v-if="type === 'REPLY'"
          outlined
        >
          拒絕
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  props: ["show", "requestData", "type"],
  computed: {
    ...mapState({
      user: "user",
    }),
    formShow: {
      get() {
        return this.show;
      },
      set(value) {
        this.$emit("update:show", value);
      },
    },
  },
  data() {
    return {
      reply: "",
      reason: "",
      replyRules: [(v) => v.length > 0 || "請輸入拒絕/接受理由"],
      reasonRules: [(v) => v.length > 0 || "請輸入協調理由"],
    };
  },
  methods: {
    ...mapActions({
      replyExchangeRequest: "replyExchangeRequest",
      makeExchangeRequest: "makeExchangeRequest",
      showAlert: "showAlert",
    }),
    replyRequest: function (status) {
      // console.log(this.requestData)
      if (!this.$refs.replyForm.validate()) {
        return;
      }
      let newRequestData = this.requestData;
      newRequestData.reply = this.reply;
      newRequestData.status = status;
      this.replyExchangeRequest(newRequestData).then((result) => {
        console.log(result);
        this.formShow = false;
      });
    },
    makeRequest: function () {
      if (!this.$refs.requestForm.validate()) {
        return;
      }
      let data = {
        reason: this.reason,
        reservation_id: this.requestData.id,
        requester_id: this.user.id,
        replier_id: this.requestData.user_id,
      };
      this.makeExchangeRequest(data).then((result) => {
        console.log(result);
        this.formShow = false;
      });
    },
  },
};
</script>

<style scoped></style>
