<template>
  <v-row>
    <v-col cols="12">
      <v-list-item>
        <v-icon>mdi-circle-medium</v-icon>
        <span class="title">會議室時刻表</span>
        <v-divider></v-divider>
        <!--        {{ weekdayOffsets }}-->
      </v-list-item>
    </v-col>
    <v-col cols="12">
      <v-card>
        <v-card-text>
          <my-date-picker
            :date-model.sync="choseDate"
            :picker-switch="date_picker"
            label="選擇日期"
          ></my-date-picker>
          <v-checkbox label="我的會議" v-model="showMyMeeting"></v-checkbox>
          <v-divider></v-divider>
          <v-tabs>
            <v-tab
              v-for="(location, index) in locations"
              :id="location['location_name']"
              :key="index"
              @click="changeTab(location['id'])"
            >
              {{ location["location_name"] }}
            </v-tab>
          </v-tabs>
          <!--          {{ weeklyBooking }}-->
          <v-simple-table>
            <thead>
              <tr>
                <th></th>
                <th
                  v-for="(weekdayOffset, index) in weekdayOffsets"
                  :key="index"
                >
                  <v-row>
                    <v-col>
                      {{
                        ($vuetify.breakpoint.sm ? "" : "星期") +
                        weekdays[
                        (choseDateToDate.getDay() + weekdayOffset) % 7
                          ].toString()
                      }}
                      <span class="d-none d-md-none d-lg-flex">
                        {{ calculatedDate(weekdayOffset) }}
                      </span>
                    </v-col>
                  </v-row>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="hour in hours" :key="hour">
                <td>
                  <span> {{ hour }} </span>
                </td>
                <td
                  v-for="(weekdayOffset, index) in weekdayOffsets"
                  :key="index"
                >
                  <meeting-card
                    v-for="(filteredReservation, index) in weeklyReservation[
                      weekdayOffset
                    ].filter((reservation) => {
                      return inTime(
                        reservation.start_time,
                        reservation.end_time,
                        hour
                      );
                    })"
                    :key="index"
                    :reservation="filteredReservation"
                    v-show="
                      showMyMeeting
                        ? filteredReservation.user_id === user.id
                        : true
                    "
                  >
                  </meeting-card>
                </td>
              </tr>
            </tbody>
          </v-simple-table>
        </v-card-text>
      </v-card>
    </v-col>
    <!--    {{ weeklyBooking }}-->
  </v-row>
</template>

<script>
import { mapActions, mapState } from "vuex";
import MeetingCard from "@/components/MeetingCard";
import MyDatePicker from "@/components/MyDatePicker";

export default {
  components: { MyDatePicker, MeetingCard },
  mounted: async function () {
    await this.locationsRefresh();
    await this.reservationsRefresh().then(() => {
      // this.changeTab(this.location_id);
    });
  },
  computed: {
    ...mapState([
      "locations",
      "reservations",
      "meetingRooms",
      "hours",
      "weekdays",
      "user",
    ]),
    filteredRoomIDs: function () {
      let filterRooms = this.meetingRooms.filter(
        (room) => room.location_id === this.location_id
      );
      return filterRooms.map((book) => book.id);
    },
    choseDateToDate: function () {
      return new Date(this.choseDate);
    },
    weeklyReservation: function () {
      // 0 is mapping chose date, and 6 is the last day of week
      let reservations = { 0: [], 1: [], 2: [], 3: [], 4: [], 5: [], 6: [] };
      let choseDate = new Date(this.choseDate);
      this.reservations.forEach((reservation) => {
        if (!this.filteredRoomIDs.includes(reservation["room_id"])) {
          return;
        }
        let use_date = new Date(reservation.use_date);
        if (this.dateInWeekRange(reservation.use_date)) {
          let weekdayOffset = (use_date - choseDate) / 86400000;
          if (0 <= weekdayOffset && weekdayOffset <= 6) {
            reservations[weekdayOffset].push(reservation);
          }
        }
      });
      return reservations;
    },
    weekdayOffsets: function () {
      return [0, 1, 2, 3, 4, 5, 6];
    },
  },
  watch: {
    locations: {
      handler: function () {
        this.location_id = this.locations.length ? this.locations[0].id : -1;
      },
      deep: true,
    },
  },
  data() {
    return {
      place: "",
      choseDate: new Date().toLocaleDateString("zh-tw").replaceAll("/", "-"),
      location_id: -1,
      date_picker: false,
      showMyMeeting: "",
    };
  },
  methods: {
    ...mapActions({
      reservationsRefresh: "reservationsRefresh",
      locationsRefresh: "locationsRefresh",
      showAlert: "showAlert",
    }),
    // tab update function
    changeTab: function (tabLocationID) {
      this.location_id = tabLocationID;
    },
    // check time in the period
    inTime: function (startTimeFormat, endTimeFormat, hourFormat) {
      let hour = parseInt(hourFormat.split(":")[0]);
      let startTime = parseInt(startTimeFormat.split(":")[0]);
      let endTime = parseInt(endTimeFormat.split(":")[0]);
      // console.log(startTime, endTime, hour, startTime <= hour && hour <= endTime);
      return startTime <= hour && hour <= endTime;
    },
    dateInWeekRange: function (dateFormat) {
      let checkDate = new Date(dateFormat);
      let startDate = new Date(this.choseDate);
      let endDate = new Date(this.choseDate);
      endDate.setDate(endDate.getDate() + 6);
      return startDate <= checkDate && checkDate <= endDate;
    },
    // return date got calculated by weekday
    calculatedDate: function (offset) {
      let choseDate = new Date(this.choseDate);
      choseDate.setDate(choseDate.getDate() + offset);
      return choseDate.toLocaleDateString("zh-tw");
    },
  },
};
</script>

<style scoped></style>
